      Результаты какой ты Лёнич)
    </h1>

    <img class="summary__image" src="../../static/pic/summary.png" />

    <div class="summary__side-logo" />

    <ul class="summary__stats">
      <li
        v-for="item in summary"
        :key="item.name"
      >
        <span class="summary__item-persents">
          {{getPersents(item)}}%
        </span>

        <span class="summary__item-name">
          {{item.name}}
        </span>
      </li>
    </ul>

    <div class="summary__button-container">
      <div class="summary__button-inner-container">
        <Button
          :text="'next podgon)'"
          :action="goToNextStep"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import Button from '@/components/common/Button';


export default {
  name: "Summary",
  components: { Button },
  methods: {
    ...mapMutations("main", ["changeStep"]),
    goToNextStep() {
      window.location.href = 'landing.html';
    },
    getPersents(item) {
      return Math.ceil(item.points * 100 / (item.maxPoints - 1));
    }
  },
  computed: {
    ...mapState("main", ["summary"]),
  }
};
</script>

