<template>
  <button
    :class="['button', `button_${color}`]"
    :disabled="isDisabled"
    @click="onClick"
  >
    <span>{{text}}</span>
  </button>
</template>

<script>
export default {
  name: "Button",
  props: {
    action: Function,
    text: String,
    isDisabled: Boolean,
    color: {
      type: String,
      default: ''
    },
  },
  methods: {
    onClick(event) {
      event.preventDefault();

      this.action();
    }
  },
};
</script>

<style lang="scss">
.button {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  height: 88px;
  border-radius: 14px;
  background-color: #fff;
  box-shadow: 10px 10px 14px 0px #eb3665;
  border-color: #fff;
  cursor: pointer;
  padding-right: 70px;

  &:disabled {
    cursor: not-allowed;
    opacity: .75;
  }

  &:hover {
    opacity: .75;
  }

