    font-weight: 900;
    color: rgba(235,54,101,1);
  }

  &_inverted {
    justify-content: flex-start;
    box-shadow: none;
    border-color: transparent;
    background-color: transparent;

    span {
      color: #000;
    }    
  }
}
</style>
