import QuestionsMap from '../../static/scheme/questionMap.json';


export default {
  namespaced: true,

  state: {
    step: 1,
    questionsMap: QuestionsMap,

    currentQuestion: null,
    currentThemeSummary: null,

    currentQuestionThemeNumber: 0,
    currentQuestionNumber: 0,
    
    questionsThemesLength: 1,
    questionsLength: 1,

    summary: [],
  },

  mutations: {
    changeStep(state, { value }) {
      state.step = value;
    },
    initQuestionsMap(state) {
      state.questionsLength = state.questionsMap[0].list.length;
      state.questionsThemesLength = state.questionsMap.length;
      
      state.currentQuestion = state.questionsMap[0].list[0];
      state.currentThemeSummary = state.questionsMap[0].summary;

      state.summary = state.questionsMap.map(item => ({
        name: item.summary.name,
        maxPoints: item.list.length,
        points: 0,
      }));
    },
    increasePoints(state) {
      state.summary[state.currentQuestionThemeNumber].points += 1;
    },
    goToNextQuestionTheme(state) {
      state.currentQuestionThemeNumber += 1;
      state.currentQuestionNumber = 0;
      state.questionsLength = state.questionsMap[state.currentQuestionThemeNumber].list.length;
      state.currentQuestion = state.questionsMap[state.currentQuestionThemeNumber].list[0];
      state.currentThemeSummary = state.questionsMap[state.currentQuestionThemeNumber].summary;
    },
    goToPreviousQuestionTheme(state) {
      state.currentQuestionThemeNumber -= 1;
      state.currentQuestionNumber = 0;
      state.currentQuestion = state.questionsMap[state.currentQuestionThemeNumber].list[0];
    },
    goToNextQuestion(state) {
      state.currentQuestionNumber += 1;
      state.currentQuestion = state.questionsMap[state.currentQuestionThemeNumber].list[state.currentQuestionNumber];
    },
    goToPreviousQuestion(state) {
      state.currentQuestionNumber -= 1;
      state.currentQuestion = state.questionsMap[state.currentQuestionThemeNumber].list[state.currentQuestionNumber];
    }
  },
};
