<template>
  <div class="quiz">
    <div class="quiz__video-wrapper">
      <video class="quiz__video" preload="auto" autoplay="true" loop="true" muted="muted">
        <source src="../../static/vid/boy.mp4" type="video/mp4">
      </video>

      <span>
        Happy birthday epta
      </span>
    </div>
    <div class="quiz__side-logo" />

    <div class="quiz__main-container">
      <div class="quiz__main">
        <header class="quiz__counter">
          {{currentQuestionNumber + 1}}/{{questionsLength}}
        </header>

        <div v-if="!isThemeSummaryShowed" class="quiz__content">
          <div v-if="!isActionsShowed" class="quiz__question-container">
            <div class="quiz__question-img">
              <img
                v-if="currentQuestion.type === 'img'"
                alt="вопросик м)"
                :src="getQuestionImgUrl(currentQuestion.fileName)"
              />
            </div>

            <span>
              {{currentQuestion.subtitle}}
            </span>
          </div>

          <div v-else class="quiz__question-container">
            <div class="quiz__question-img">
              <img
                v-if="currentQuestion.answerType && currentQuestion.answerType === 'gif'"
                alt="ответик м)"
                :src="getAnswerGifInImgUrl(`${currentQuestion.fileName.split('.')[0]}.gif`)"
              />

              <img
                v-else-if="currentQuestion.type === 'img'"
                alt="ответик м)"
                :src="getAnswerImgUrl(currentQuestion.fileName)"
              />
            </div>

            <span>
              {{currentQuestion.afterAnswerMsg}}
            </span>
          </div>

          <ul
            v-if="!isActionsShowed"
            class="quiz__answers"
            @click="selectAnswer"
          >
            <li
              v-for="(item, index) in currentQuestion.answers"
              :key="item"
            >
              {{index + 1}}. {{item}}
            </li>
          </ul>
        </div>

        <div v-else class="quiz__answer-container">
          <div class="quiz__answer">
            <img
              v-if="currentThemeSummary.type === 'gif'"
              alt="рофл итог)"
              :src="getSummaryGifUrl(currentThemeSummary.fileName)"
            />
          </div>

          <span class="quiz__answer-main">
            {{currentThemeSummary.name}}
          </span>

          <span class="quiz__answer-subtitle">
            {{currentThemeSummary.subtitle}}
          </span>
        </div>

        <footer v-if="isActionsShowed" class="quiz__actions">
          <div class="quiz__action">
            <Button
              v-if="currentQuestionNumber !== 0"
              :text="'nazad'"
              :color="'inverted'"
              :action="handleGoToPreviousQuestion"
            />
          </div>

          <div class="quiz__action">
            <Button
              :text="'next'"
              :action="handleGoToNextQuestion"
            />
          </div>
        </footer>

        <footer v-if="isThemeSummaryShowed" class="quiz__actions_solo">
          <div class="quiz__action_solo">
            <Button
              :text="'next tema'"
              :action="handleGoToNextTheme"
            />
          </div>
        </footer>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import Button from '@/components/common/Button';


export default {
  name: "Quiz",
  components: { Button },
  data() {
    return {
      isActionsShowed: false,
      isThemeSummaryShowed: false,
    };
  },
  computed: {
    ...mapState(
      "main", 
      [
        "currentQuestion",
        "currentQuestionNumber",
        "questionsLength",
        "currentThemeSummary",
        "currentQuestionThemeNumber",
        "questionsThemesLength",
      ]
    ),
  },
  methods: {
    ...mapMutations(
      "main",
      [
        "changeStep",
        "increasePoints",
        "goToNextQuestionTheme",
        "goToPreviousQuestionTheme",
        "goToNextQuestion",
        "goToPreviousQuestion"
      ],
    ),
    getQuestionImgUrl(value) {
      var images = require.context('../../static/pic/questions/', false, /\.png$/)

      return images('./' + value)
    },
    getAnswerImgUrl(value) {
      var images = require.context('../../static/pic/answers/', false, /\.png$/)

      return images('./' + value)
    },
    getAnswerGifInImgUrl(value) {
      var images = require.context('../../static/pic/answers/', false, /\.gif$/)

      return images('./' + value)
    },
    getSummaryGifUrl(value) {
      var gifs = require.context('../../static/gif/', false, /\.gif$/)

      return gifs('./' + value)
    },
    selectAnswer(event) {
      const value = event.target.textContent;

      if (value.includes(this.currentQuestion.answer)) {
        this.increasePoints();
      }

      event.target.style.backgroundColor = '#EB3665';
      event.target.style.color = '#fff';

      setTimeout(() => {
        this.isActionsShowed = true;
      }, 500);
    },
    handleGoToNextQuestion() {
      if ((this.currentQuestionNumber + 1) === this.questionsLength) {
        this.isThemeSummaryShowed = true;
        this.isActionsShowed = false;
      } else {
        this.goToNextQuestion();
        this.isActionsShowed = false;
      }
    },
    handleGoToPreviousQuestion() {
      this.goToPreviousQuestion();
      this.isActionsShowed = false;
    },
    handleGoToNextTheme() {
      if ((this.currentQuestionThemeNumber + 1) !== this.questionsThemesLength) {
        this.isThemeSummaryShowed = false;
        this.goToNextQuestionTheme();
      } else {
        this.changeStep({ value: 3 });
      }
    }
  },
};
</script>

